<template>
  <v-app>
    <NavbarMenu />
    <div class="content">
      <v-container style="margin-top: 30px" class="mb-12">
        <v-card
          v-for="(ques, index) in questions"
          :key="ques.questionId"
          class="ques_item"
        >
          <v-container fluid v-if="rs[index]">
            <p>
              <span>{{ index + 1 }}.[{{ ques.diseaseName }}]</span>
              {{ ques.questionInfo }}({{ ques.score }} 分)
            </p>
            <!-- <v-radio-group v-model="choice"> -->
            <!-- <v-radio-group> -->
            <v-radio-group v-model="rs[index].value">
              <v-radio :label="ques.choiceA" color="red" value="1"></v-radio>
              <!-- @change="changeResult(ques.questionId,value)" -->
              <v-radio :label="ques.choiceB" color="red" value="2"></v-radio>
              <v-radio :label="ques.choiceC" color="red" value="3"></v-radio>
              <v-radio :label="ques.choiceD" color="red" value="4"></v-radio>
            </v-radio-group>
          </v-container>
        </v-card>
        <div class="text-center">
          <v-btn
            rounded
            color="primary"
            dark
            @click="getscore()"
            style="margin-top: 20px"
          >
            查看分数
          </v-btn>
          <v-btn
            rounded
            color="primary"
            dark
            @click="use()"
            style="margin-top: 20px"
          >
            刷新
          </v-btn>
        </div>
      </v-container>
    </div>
  </v-app>
</template>

<script>
import axios from "axios";
import qs from "qs";
// import Vue from 'vue'
export default {
  name: "ExamItem",
  inject:['reload'],
  components: {
    NavbarMenu: () => import("../components/NavbarMenu"),
  },
  data() {
    return {
      choice: [],
      // rsesults: [],
      questions: [
        {
          questionId: "011",
          questionInfo: "题干1",
          diseaseName: "病种名1",
          score: "10",
          // choice: [{ choice: "aaa1",value: "A" },{ choice: "",value: 1 },{ choice: "",value: 1 },{ choice: "",value: 1 },],
          choiceA: "aaa1",
          choiceB: "bbb1",
          choiceC: "ccc1",
          choiceD: "ddd1",
        },
        {
          questionId: "012",
          questionInfo: "题干2",
          diseaseName: "病种名2",
          score: "10",
          choiceA: "aaa2",
          choiceB: "bbb2",
          choiceC: "ccc2",
          choiceD: "ddd2",
        },
      ],
      rs: [],
    };
  },
  created() {
    // this.updata();
    // this.questions.forEach((item) => {
    //   this.rs.push({
    //     questionId: item.questionId,
    //     value: "A",
    //   });
    // });
    // console.log("rs:", this.rs);
  },
  mounted() {
    this.updata();
  //   // console.log("id:", this.$route.query.id);
    console.log('questions:',this.questions)
    this.questions.forEach((item) => {
      this.rs.push({
        questionId: item.questionId,
        value: "",
      });
    });
    // console.log("rs:", this.rs);
  },
  methods: {
    use() {
      // this.$set(this.questions,2,this.questions[1])
      // this.questions[0].questionInfo = 'fkjsdalkjf'
        this.questions.push(
          {
          questionId: "013",
          questionInfo: "题干2",
          diseaseName: "病种名2",
          score: "10",
          choiceA: "aaa2",
          choiceB: "bbb2",
          choiceC: "ccc2",
          choiceD: "ddd2",
        }
        );
        // // location.reload()
        // this.reload()
        console.log(this.questions)
        // console.log(this.questions[1]===this.questions[2])
        // this.$forceUpdate();
    },
    updata() {
      axios
        .post("/test/paper", qs.stringify({ testId: this.$route.query.id }))
        .then((res) => {
          console.log("res=>", res);
          if (res.data.code == 200) {
            this.questions = res.data.data;
            console.log(res.data.data);
          } else {
            this.$store.dispatch("snackbar/openSnackbar", {
              msg: res.data.msg,
              color: "primary",
            });
          }
        })
        .catch((err) => {
          console.log('err',err.data);
        });
    },
    getscore() {
      console.log("rs1:", this.rs);
      // this.questions.forEach((item) => {
      //   this.rs.push({
      //     questionId: item.questionId,
      //     value: "A",
      //   });
      // });
      axios
        .post(
          "/test/score",
          qs.stringify({
            result: this.rs,
          })
        )
        .then((res) => {
          console.log("res:", res);
          if (res.data.code === 200) {
            this.$store.dispatch("snackbar/openSnackbar", {
              msg:
                "您的分数为：" +
                res.data.data.score +
                " / " +
                res.data.data.sumscore,
              color: "primary",
            });
          } else {
            console.log("错误代码：", res.data.code);
          }
        })
        .catch((err) => {
          console.log("err=>", err);
        });
      // console.log(this.questions[index]);
      // console.log(this.choice);
    },
  },
};
</script>


